import React, { Component } from 'react';
import axios from 'axios';
import '../../assets/css/error.css';
class Error extends Component {
    estiloestado() {
        if (this.props.error.Estado === "1") {
            return {
                backgroundImage: "url(https://annalectbox.com.gt/AdOps/ErroresPlataformas/img/icon/estado2.png)",
                backgroundSize: '30%',
                backgroundRepeat: 'no-repeat',
                backgroundPosition: '50%',
                backgroundColor: 'transparent',
                border: '0px',
                height: '50px'
            }
        } else {
            return {
                backgroundImage: "url(https://annalectbox.com.gt/AdOps/ErroresPlataformas/img/icon/estado22.png)",
                backgroundSize: '30%',
                backgroundRepeat: 'no-repeat',
                backgroundPosition: '50%',
                backgroundColor: 'transparent',
                border: '0px',
                height: '50px'
            }
        }
    }
    clickMe(error) {
        axios
            .post("https://annalectbox.com.gt/AdOps/ApiErrors/actualizarerrores.php?id=" + error.iderror)
            .then(response => {
            })
            .catch(error => {
                console.log(error);
            })
    }
    render() {
        const { error } = this.props;

        if (error.Media === this.props.medio) {

            return (

                <div className="col-12 m-2 contendedordelerror p-0" estado={error.GrupoError} tipouser={error.tipousuario}>
                    <div className="tipodeerror col-12 text-left m-0 font-weight-bold text-primary card-header py-1" id="marca">{error.cuenta} <span>{error.idcuenta} </span></div>
                    <div className="estadoerror" style={{ display: 'none' }} >{error.Estado}</div>
                    
                    <div id="contenedorerror" className="row col-12 m-auto pt-1 pb-1"  >
                        <div id="imagenass" className="col-12 col-sm-12 col-md-2 col-xl-1">
                            <img src={"https://annalectbox.com.gt/AdOps/ErroresPlataformas/img/icon/" + error.Icono} alt="asdf" /> </div>
                        <div id="datoserrores" className=" col-md-4 col-12 col-sm-6 col-md-3 col-xl-4">
                            <p id="nombreerror" className="text-left font-weight-bold mb-1"> {error.DescripcionError} <span id="fechaerror" className="ml-2 font-weight-normal">{error.fecha}</span></p>
                            <p id="datoerror" className="text-break mb-1">{error.Campaingname}</p>
                        </div>
                        <div id="texterror" className="col-12 col-sm-6 col-md-3 text-center">

                            <p className="font-weight-bold">Error</p>
                            <p id="errortext" className="text-break">{error.Error}</p>
                        </div>
                        <div id="nombreerror" className="col-12 col-sm-6 col-md-2">
                            <p className="text-center font-weight-bold">{error.Comentario}</p>
                        </div>

                        <button onClick={this.clickMe.bind(this, error)} id="imagenas" className="col-8 col-sm-2 col-md-1 aquelestil" style={this.estiloestado()}></button>

                    </div>
                </div>
            )

        } else {
            return (
                <div className="display:none;"></div>
            )
        }
    }
}
export default Error;